<template id="">
  <fragment >
    <tr @click.shift="activate(children[0].title, true)" @click.exact="activate(children[0].title)" :class="{active: active, dnone:cshow}">
      <td class="border-none" v-for="i in ofset" :key="i"></td>
      <td v-if="types=='list'" colspan="100%">{{name}}
        <img v-if="show" src="@/assets/img/Arrow1.png" height="4px" alt="">
        <img v-else src="@/assets/img/Arrow2.png" width="4px" height="8px" alt="">
      </td>
      <td v-else="" v-for="(col, index, num) in children[0]"  :key="'col'+index">{{children[0][table_coolumns[num+ofset].name]}} </td>
    </tr>
    <RowComponent :selectPlanets="selectPlanets" :selectRowTable="selectRowTable" :table_coolumns="table_coolumns" :cshow="showFun" v-for="(row, index) in childrens_par"  :columns="row.columns" :name="row.name" :ofset="ofset+1" :children="row.childrens" :types="row.type" :key="'row'+ofset+index"></RowComponent>
 </fragment>
</template>

<script type="text/javascript">
  export default{
    name: "RowComponent",
    props:[
      "name",
      "types",
      "ofset",
      "children",
      "columns",
      "cshow",
      "table_coolumns",
      "selectPlanets",
      "selectRowTable",
    ],
    data(){
      return{
        // active: false,
        show: false,
      }
    },
    methods:{
      activate(name, shift){
        if (this.types=="row") {
          this.selectPlanets(name, shift)
        }
        if (this.types=="list") {
          this.show = !this.show
        }

      }
    },
    computed:{
      active(){
        return this.selectRowTable[this.children[0].title]
      },
      showFun(){
        if (this.types=='list' && this.cshow) {
          return this.cshow
        }
        return this.show
      },
      childrens_par(){
        if (this.types == 'list' ) {
          return this.children
        }
        return []
      }
    },
    watch:{
    }
  }
</script>
<style media="screen" scoped="">
  .dnone{
    display: none;
  }
  tr:hover>td{
    cursor: pointer;
    background: #0000001c;
  }
  .active>td{
    background: #006e791f!important;
    color: white;
  }
  .border-none{
    background: none!important;
  }
</style>
